body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

footer {
  color: black;
  font-size: 20px;
  background-image: url('./assets/images/bg-footer.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-style {
  color: white;
  background-color: forestgreen; 
}

.g-card {
  margin: 20px;
}

.g-card-image {
  border-radius: 10px;
  width: 300px;
  height: 480px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.g-card-info {
  margin-top: 10px;
  min-height: 100px;
}

.g-card-title {
  font-size: 24px;
  margin: 0px;
}

.g-card-sub-title {
  font-size: 16px;
  margin: 0px;
}

.about-profile {
  border-radius: 10px;
  width: 280px;
  height: 460px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.about-skill {
  border-radius: 10px;
  max-width: 200px;
  max-height: 200px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.about-skill-tittle {
  font-size: 50px;
  font-weight: bold;
  margin-bottom: 10px;
}

.success-msg {
  color: #57A773;
  padding: 10px 15px;
}

.err-msg {
  color: #EE6352;
  padding: 10px 15px;
}

.whats-api-btn {
  position: fixed;
  right: 26px;
  bottom: 26px;
  width: 60px;
  height: 60px;
  z-index: 1000;
  background: transparent;
  background-image: url('./assets/images/whatsapp.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-color: transparent;
}

.facebook-icon {
  color: blue;
  width: 30px;
  height: 30px;
  margin-left: 10px;
}
.facebook-icon:hover {
  color: lightsteelblue;
}

.twitter-icon {
  color: lightskyblue;
  width: 30px;
  height: 30px;
}
.twitter-icon:hover {
  color: lightsteelblue;
}

.github-icon {
  color: black;
  width: 30px;
  height: 30px;
}
.github-icon:hover {
  color: lightsteelblue;
}

.linkedin-icon {
  color: blue;
  width: 30px;
  height: 30px;
}
.linkedin-icon:hover {
  color: lightsteelblue;
}